  }
}

.base-list-heading {
  font-size: 44px;
  font-weight: 500;
  margin-bottom: 50px;
}
@media screen and (max-width: 460px) {
  .base-list-heading {
    font-size: 30px;
    margin-bottom: 30px;
  }
}

.base-list-container {
  display: flex;
  flex-direction: column;
  gap: 35px;
}

.base-list-container > div {
  display: flex;
  align-items: center;
  gap: 50px;
}
@media screen and (max-width: 460px) {
  .base-list-container > div {
    flex-direction: column;
    gap: 20px;
  }
}

.base-list-container > div > div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.base-list-container > div > div > h2 {
  font-size: 33px;
  line-height: 126%;
  font-weight: 500;
  margin-bottom: 20px;
}
@media screen and (max-width: 460px) {
  .base-list-container > div > div > h2 {
