  font-size: 18px;
  font-weight: 400;
  color: #161618;
  opacity: 60%;
}
@media screen and (max-width: 460px) {
  .base-list-container > div > div > p {
    font-size: 14px;
    line-height: 126%;
    letter-spacing: -0.01em;
  }
}

.base-list-container > div > img {
  border-radius: 45px;
}
@media screen and (max-width: 460px) {
  .base-list-container > div > img {
    width: 100%;
    border-radius: 18px;
    height: 260px;
    object-fit: cover;
  }
}

.base-list-button {
  display: inline-block;
  margin: 60px auto;
  margin-bottom: 0;
  padding: 16px 30px;
  background-color: #20222c;
  color: white;
  border-radius: 10px;
  font-size: 20px;
  cursor: pointer;
}
.base-list-button-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}
@media screen and (max-width: 460px) {
  .base-list-button {
    width: 100%;
    margin-top: 20px;
    font-size: 15px;
  }
}

.base-list-listing {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 14px;
  line-height: 130%;
  color: #161618;
}

